//Import dependencies
import React, {Fragment, useState, useEffect} from 'react';
import {connect} from 'react-redux';
import axios from 'axios';
import 'bulma';
import './index.scss';

//Import components
import Navbar from '../Navbar';


//Map State to Props
const mapStateToProps = (state) => {
    return {}
}

//Build container
const TeacherEdit = (props) => {
    const id = props.match.params.id;
    const [teacher,
        setTeacher] = useState({
        name: {
            first: '',
            last: ''
        },
        email: '',
        social: {
            facebook: '',
            linkedin: '',
            twitter: ''
        },
        about: '',
        courses: [],
        avatar: {
            bucket: '',
            key: '',
            url: ''
        }
    });

    const [firstName,
        setFirstName] = useState('');
    const [lastName,
        setLastName] = useState('');
    const [email,
        setEmail] = useState('');
    const [about,
        setAbout] = useState('');
    const [facebook,
        setFacebook] = useState('');
    const [linkedin,
        setLinkedin] = useState('');
    const [twitter,
        setTwitter] = useState('');
    const [fileName,
        setFileName] = useState('No file chosen');

    useEffect(() => {
        const main = async() => {
            let response = await axios.get(`/api/teacher/${id}`);
            let teacherData = response.data;
            setFirstName(teacherData.name.first);
            setLastName(teacherData.name.last);
            setEmail(teacherData.email);
            setAbout(teacherData.about);
            setFacebook(teacherData.social.facebook);
            setLinkedin(teacherData.social.linkedin);
            setTwitter(teacherData.social.twitter);
            setTeacher(teacherData);
        }
        main();
    }, [id])

    const handleSubmit = async(event) => {
        event.preventDefault();
        let form = document.getElementById('editTeacher');
        let payload = new FormData(form);

        try {
            const res = await axios.put(`/api/teacher/${id}`, payload);
            window
                .location
                .reload();
            window.alert(res.data.success.desc);
        } catch (err) {
            const {error} = err.response.data;
            window.alert(error.desc);
            console.log(error);
        }
    }

    return (
        <Fragment>
            <Navbar/>
            <section className="teacher-edit">
                <div className="container">
                    <div className="is-flex heading">
                        <h1 className="has-text-centered is-size-3">Edit Teacher</h1>
                        <div className="buttons">
                            <button form="editTeacher" type="submit" className="button is-success">Save</button>
                            <a href={`/teachers/${id}/view`}><button className="button is-info">View Profile</button></a>
                        </div>
                    </div>
                    <form
                        id="editTeacher"
                        onSubmit={handleSubmit}
                        className="form"
                        encType="multipart/form-data">
                        <label>Name</label>
                        <div className="field is-horizontal">
                            <input
                                type="text"
                                className="input first"
                                name="firstname"
                                placeholder="First Name"
                                value={firstName}
                                onChange={(e) => {e.preventDefault(); setFirstName(e.target.value)}}
                                required/>
                            <input
                                type="text"
                                className="input"
                                name="lastname"
                                placeholder="Last Name"
                                value={lastName}
                                onChange={(e) => {e.preventDefault(); setLastName(e.target.value)}}/>
                        </div>
                        <label>Email</label>
                        <input
                            type="email"
                            className="input item"
                            name="email"
                            placeholder="Email Id"
                            value={email}
                            onChange={(e) => {e.preventDefault(); setEmail(e.target.value)}}
                            required/>
                        <label>About Teacher</label>
                        <textarea
                            className="textarea item"
                            name="about"
                            placeholder="About Teacher. e.g. I am a professional poker player for 6years..."
                            value={about}
                            onChange={(e) => {
                            e.preventDefault();
                            setAbout(e.target.value)
                        }}/>
                        <label>Facebook Profile</label>
                        <input
                            type="url"
                            className="input item"
                            name="facebook"
                            placeholder="Facebook"
                            value={facebook}
                            onChange={(e) => {
                            e.preventDefault();
                            setFacebook(e.target.value)
                        }}/>
                        <label>Linkedin Profile</label>
                        <input
                            type="url"
                            className="input item"
                            name="linkedin"
                            placeholder="Linkedin"
                            value={linkedin}
                            onChange={(e) => {
                            e.preventDefault();
                            setLinkedin(e.target.value)
                        }}/>
                        <label>Twitter Profile</label>
                        <input
                            type="url"
                            className="input item"
                            name="twitter"
                            placeholder="Twitter"
                            value={twitter}
                            onChange={(e) => {
                            e.preventDefault();
                            setTwitter(e.target.value)
                        }}/>
                        <div className="file has-name is-fullwidth">
                            <figure className="image is-128x128">
                                <img className="is-rounded display-pic" src={teacher.avatar.url} alt='dp'/>
                            </figure>
                            <label className="file-label">
                                <input
                                    className="file-input"
                                    type="file"
                                    name="avatar"
                                    onChange={(e) => {
                                    setFileName(e.target.files[0].name)
                                }}/>
                                <span className="file-cta">
                                    <span className="file-label">
                                        Upload New Image
                                    </span>
                                </span>
                                <span className="file-name">{fileName}</span>
                            </label>
                        </div>
                    </form>
                </div>
            </section>
        </Fragment>
    )
}

//Connect with redux and export
export default connect(mapStateToProps)(TeacherEdit);