import axios from 'axios'

export default async ( email, password, dispatch, logIn ) => {
        try {
            const res = await axios.post('/api/auth/local', { email, password });
            const data = await res.data;
            const { name, permission, expiry } = data.payload;
            dispatch(logIn(name, permission, expiry));
            window.location.reload();
            return [ "success", data ];
        } catch (err) {
            return [ "failed", err.response.data ];
        }
}