import { LOG_IN, LOG_OUT } from '../actions/types';

const initialState = {
    user: {
        isLoggedIn: false,
        name: '',
        permission: '',
        expiry: 0
    }
}

export default (state=initialState, action={}) => {
    const type = action.type;
    const payload = action.payload;
    switch(type) {
        case LOG_IN :
            return {...state, user: { ...payload } };
        case LOG_OUT :
            return {...state, user: { ...payload }};
        default :
            return state;
    }
}
