import React, {useState, useEffect} from 'react';
import {Route} from 'react-router-dom';

import Login from '../Login';
import Loading from './Loading';

import auth from '../../auth';

export default({
    component: Component,
    ...rest
}) => {

    const [isAuth,
        setIsAuth] = useState(null)

    useEffect(() => {
        const main = async() => {
            const authStatus = await auth.isAuthenticated();
            setIsAuth(authStatus);
        }

        main()
    }, [])

    // return (
    //     <Route
    //         {...rest}
    //         render={(props) => (isAuth
    //         ? <Component {...props}/>
    //         : <Login/>)}/>
    // )

    if (isAuth === null) {
        return <Route component={Loading}/>
    } else {
        return (
            <Route
                {...rest}
                render={(props) => (isAuth
                ? <Component {...props}/>
                : <Login/>)}/>
        )
    }

}